
@import url('https://fonts.googleapis.com/css?family=Roboto');
body {
  margin: 0;
  background-color: rgb(24, 22, 22);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.content{
  font-family: 'lato';
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vh;
  height: 60px;
  font-size: 36px;
  height:0;
  margin-bottom:0;
  letter-spacing: 1.8px;
}

.App {
  width:80vw;
  height:80vh;
  margin:auto auto;
}
img{
  margin-top: 3%;
  max-width: 100%;
  max-height: 100%;
}
.language-toggler{
  position: absolute;
  top: 1vh;
  left:2vw;
  width: 120px;
  height: 25px;
  display:flex;
  border: 1px solid rgb(192, 192, 192);
  border-radius: 25px;
  background-color: rgb(48, 48, 48);
  justify-content: space-between;
  align-items: center;
}
.language-tag{
  width:30px;
  height:28px;
  font-family: 'Roboto';
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
}
.button-field{
  width: 55px;
  height: 3px;
  background-color: rgb(128, 128, 128);
  border-radius: 3px;
}
.language-button{
  width: 25px;
  height: 25px;
  border-radius: 25px;
  margin-top: -11.2px;
  background-color: rgb(192, 192, 192);  
}
.en{
  margin-left: 0;
}
.pl{
  margin-left: 30px;
}
.image{
  width: 90vh;
  height:90vh;
}